<template>
	<view class="main">
		<view class="content">
			<scroll-view scroll-y="true" style="height: 100%" @scrolltolower="onToButtom" :refresher-threshold="40">
				<view class="search-box">
					<img
						src="data:image/svg+xml;charset=utf-8;base64,"
						alt=""
					/>
					<input v-model="searchValue" placeholder="搜索" />
					<uni-icons style="margin: auto" v-if="searchValue" size="24" type="close" @click="closeSearch" />
				</view>
				<view class="tag-cloud">
					<tag-cloud :rollList="rollList" @goSearch="search"></tag-cloud>
					<tag-cloud :rollList="rollList2" @goSearch="search"></tag-cloud>
				</view>
				<view class="content" @click="goToDetail">
					<view class="list">
						<view class="articlesList" v-for="(item, index) in tableData" :key="index">
							<view class="image-box">
								<image mode="widthFix" :src="item.image" alt="" />
							</view>
							<view class="title">{{ item.title }}</view>
							<view class="introduction">{{ item.desc }}</view>
							<!-- <view class="icon">
								<img
									src="data:image/svg+xml;charset=utf-8;base64,"
									alt=""
								/>
								<text>123</text>
								<img
									src="data:image/svg+xml;charset=utf-8;base64,"
									alt=""
								/>
								<text>222</text>
							</view> -->
							<view class="introduction">{{ item.publishTime }}</view>
						</view>
						<view class="load-out">
							<view v-if="tableData.length > 0 || loadingStatus == 'no-more'">
								<uni-load-more iconType="circle" :status="loadingStatus" :contentText="contentText" />
							</view>
						</view>
					</view>
				</view>
			</scroll-view>
		</view>
		<view class="go-top" v-if="isShow">
			<uni-icons style="margin: auto" size="24" type="arrow-up" @click="goTop" />
		</view>
		<uni-popup ref="message" type="message">
			<uni-popup-message type="error" :message="messageText" :duration="2000"></uni-popup-message>
		</uni-popup>
		<loading :loading="showLoading" :firstContent="firstContent" :secondContent="secondContent" />
	</view>
</template>

<script>
import tagCloud from './components/tag-cloud.vue';
import { newsList } from '@/common/api/business.js';
export default {
	components: {
		tagCloud
	},
	data() {
		return {
			page: 1,
			// 加载信息
			loadingStatus: 'no-more',
			contentText: {
				contentdown: '上拉显示更多',
				contentrefresh: '加载中...',
				contentnomore: '没有更多数据了哦～'
			},
			showLoading: false, // 自定义遮罩显影
			firstContent: '', // 自定义遮罩第一行文本
			secondContent: '', // 自定义遮罩第二行文本
			messageText: '错误信息',
			tableData: [],
			searchValue: '', // 搜索值
			rollList: [
				{ text: 'Vue.js', size: 20, color: '#3498db', backgroundColor: 'rgba(52, 152, 219, 0.2)' },
				{ text: 'JavaScript', size: 16, color: '#2ecc71', backgroundColor: 'rgba(46, 204, 113, 0.2)' },
				{ text: 'CSS', size: 14, color: '#e74c3c', backgroundColor: 'rgba(231, 76, 60, 0.2)' },
				{ text: 'Golang', size: 22, color: '#9b59b6', backgroundColor: 'rgba(155, 89, 182, 0.2)' },
				{ text: 'UniApp', size: 19, color: '#34495e', backgroundColor: 'rgba(52, 73, 94, 0.2)' }
			],
			rollList2: [
				{ text: 'HTML', size: 18, color: '#f39c12', backgroundColor: 'rgba(243, 156, 18, 0.2)' },
				{ text: 'CSS', size: 14, color: '#e74c3c', backgroundColor: 'rgba(231, 76, 60, 0.2)' },
				{ text: 'Frontend', size: 22, color: '#9b59b6', backgroundColor: 'rgba(155, 89, 182, 0.2)' },
				{ text: 'Web Development', size: 19, color: '#34495e', backgroundColor: 'rgba(52, 73, 94, 0.2)' }
			],
			animationData: {},
			isShow: false
		};
	},
	onLoad() {
		this.newsList();
	},

	onPageScroll(e) {
		if (e.scrollTop >= 800) {
			// 显示
			this.isShow = true;
		} else {
			// 隐藏
			this.isShow = false;
		}
	},
	beforeDestroy() {
		// 在组件销毁之前清除 debounce 定时器
		clearTimeout(this.debounceTimer);
		this.debounceTimer = null;
	},
	methods: {
		/* 列表请求数据开始 */
		// 武器列表
		newsList() {
			this.setLoadingOptions(true);
			let params = {
				title: this.searchValue,
				pageNo: this.page,
				pageSize: 10,
				cid: '',
				status: -1,
				recommended: 1
			};
			newsList(params).then((res) => {
				let result = res.data;
				if (result.code === 200) {
					if (result.data.count === this.tableData.length) {
						// 数据已全部加载
						this.loadingStatus = 'no-more';
						uni.stopPullDownRefresh();
						this.setLoadingOptions(false);
						return;
					}
					// 拼接数组
					this.tableData = this.tableData.concat(result.data.lists);
					// 判断数组总量是否小于页面Size
					if (this.tableData.length == result.data.count) {
						this.loadingStatus = 'no-more';
					} else {
						this.loadingStatus = 'more';
					}
					uni.stopPullDownRefresh();
					this.setLoadingOptions(false);
				} else {
					this.messageText = result.msg;
					this.$refs.message.open();
				}
			});
		},
		/* 列表请求数据结束 */
		// 清除输入值
		closeSearch() {
			this.searchValue = null;
		},
		// 页面跳转
		goToDetail() {
			uni.navigateTo({
				url: './detail',
				animationType: 'fade-in',
				animationDuration: 300
			});
			/* uni.showToast({
					title: '功能开发中',
					icon: 'none',
					duration: 1000
				}); */
		},
		// 滚动到底部
		onToButtom() {
			if (this.loadingStatus === 'no-more') {
				return;
			}
			this.page++;
			this.loadingStatus = 'loading';
			this.getarticlesList();
		},
		// 回到顶部
		goTop() {
			uni.pageScrollTo({
				scrollTop: 0, // 滚动到页面的目标位置  这个是滚动到顶部, 0
				duration: 500 // 滚动动画的时长
			});
		},
		// 设置遮罩数据
		setLoadingOptions(flag, firstText, secondText) {
			this.showLoading = flag || false;
			this.firstContent = firstText || '';
			this.secondContent = secondText || '';
		}
	},
	// 监听搜索栏
	watch: {
		searchValue: {
			handler(newValue) {
				clearTimeout(this.debounceTimer);
				this.debounceTimer = setTimeout(() => {
					this.page = 1;
					this.tableData = [];
					this.newsList();
				}, 200); // 设置防抖时间间隔，单位为200毫秒
			},
			immediate: false,
			deep: true
		}
	}
};
</script>

